import React, { Suspense, lazy } from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";

import AppBar from "./components/AppBar/AppBar";
import routes from "./routes";

const HomePage = lazy(() => "./components/HomePage/HomePage");
const MoviesPage = lazy(() => "./components/MoviesPage/MoviesPage");
const MovieDetailsPage = lazy(
  () => "./components/MovieDetailsPage/MovieDetailsPage"
);

const App = () => (
  <>
    <AppBar />
    <Suspense fallback={<h1>Загрузка</h1>}>
      <Switch>
        <Route exact path={routes.homePage} component={HomePage} />
        <Route exact path={routes.moviesPage} component={MoviesPage} />
        <Route path={routes.movieDetailsPage} component={MovieDetailsPage} />
      </Switch>
    </Suspense>
  </>
);

export default App;
